export default function basket() {
  var d = document,
    itemBox = d.querySelectorAll(".item_box"), //   
    cartCont = d.getElementById("cart_content"); //    
  //     
  function addEvent(elem, type, handler) {
    if (elem.addEventListener) {
      elem.addEventListener(type, handler, false);
    } else {
      elem.attachEvent("on" + type, function () {
        handler.call(elem);
      });
    }
    return false;
  }
  //    LocalStorage
  function getCartData() {
    return JSON.parse(localStorage.getItem("cart"));
  }
  //    LocalStorage
  function setCartData(o) {
    localStorage.setItem("cart", JSON.stringify(o));
    return false;
  }
  //    

  function count() {
    var count = 0;
    if (getCartData()) {
      var cartData = getCartData();
      console.log(cartData);
      for (var items in cartData) {
        //console.log(cartData[items][1]+" "+ cartData[items][2]);
        count += cartData[items][2];
      }
    }
    return count;
  }

  function addItem(plus) {
    if (getCartData()) {
      var cartData = getCartData();
      var item = plus.getAttribute("data-id");
      //console.log(cartData[item][2]);
      cartData[item][2] = Number(cartData[item][2]) + 1;
      console.log(cartData[item][2]);

      setCartData(cartData);
      cartCont.innerHTML = basketGenerate();
    }
  }

  function removeItem(plus) {
    if (getCartData()) {
      var cartData = getCartData();
      var item = plus.getAttribute("data-id");
      //console.log(cartData[item][2]);
      cartData[item][2] = Number(cartData[item][2]) - 1;
      console.log(cartData[item][2]);
      if (cartData[item][2] == 0) delete cartData[item];

      setCartData(cartData);
      cartCont.innerHTML = basketGenerate();
    }
  }

  function sum() {
    var sum = 0;
    if (getCartData()) {
      var cartData = getCartData();
      console.log(cartData);
      for (var items in cartData) {
        //console.log(cartData[items][1]+" "+ cartData[items][2]);
        sum += cartData[items][1] * cartData[items][2];
        console.log(sum);
      }
    }
    return sum;
  }

  function addToCart(e) {
    this.disabled = true; //       
    var cartData = getCartData() || {}, //       ,    
      parentBox = this.parentNode.parentNode, //    &quot;  &quot;
      itemId = this.getAttribute("data-id"), // ID 
      itemTitle = parentBox.querySelector(".item_title").innerHTML, //  
      itemPrice = parentBox.querySelector(".item_price").innerHTML; //  
    if (cartData.hasOwnProperty(itemId)) {
      //      ,   +1   
      cartData[itemId][2] += 1;
    } else {
      //      ,    
      cartData[itemId] = [itemTitle, itemPrice, 1];
    }
    //    LocalStorage
    if (!setCartData(cartData)) {
      this.disabled = false; //     LS
      cartCont.innerHTML = "   .";
      setTimeout(function () {
        cartCont.innerHTML = "  : " + count();
      }, 1000);
    }
    return false;
  }
  //       &quot;  &quot;
  for (var i = 0; i < itemBox.length; i++) {
    addEvent(itemBox[i].querySelector(".add_item"), "click", addToCart);
  }

  function basketGenerate() {
    var cartData = getCartData(), //    
      totalItems = "";
    console.log(JSON.stringify(cartData));
    //  -    ,     
    if (cartData !== null) {
      totalItems =
        '<table class="shopping_list table-hover"><tr><th></th><th></th><th>-</th><th> </th><th> </th></tr>';
      for (var items in cartData) {
        totalItems += "<tr>";
        for (var i = 0; i < cartData[items].length; i++) {
          totalItems += "<td>" + cartData[items][i] + "</td>";
        }
        totalItems +=
          "<td>" +
          '<span class="plus glyphicon glyphicon-plus" data-id="' +
          items +
          '" onclick="addItem(this)"></span>' +
          "</td>";

        totalItems +=
          "<td>" +
          '<span class="minus glyphicon glyphicon-minus" data-id="' +
          items +
          '" onclick="removeItem(this)"></span>' +
          "</td>";

        totalItems += "</tr>";
      }
      totalItems +=
        "<tr>" +
        "<td>" +
        "" +
        "</td>" +
        "<td>" +
        sum() +
        "</td>" +
        "<td>" +
        count() +
        "</td>" +
        "<td></td><td></td></tr>";
      totalItems += "<table>";
      return totalItems;
    } else {
      //    ,    
      return "  : " + count();
    }
  }

  //      
  function openCart(e) {
    cartCont.innerHTML = basketGenerate();
    return false;
  }

  /*   */
  addEvent(d.getElementById("checkout"), "click", openCart);
  /*   */
  addEvent(d.getElementById("clear_cart"), "click", function (e) {
    localStorage.removeItem("cart");
    cartCont.innerHTML = " .";
  });
  sum();
}
